@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Muli:300,400,600,700);
@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
  width: 100%;
  height: 30px;
  background: #2874f0;
}
.subHeader {
  max-width: 1100px;
  width: 100%;
  height: 100%;
  display: flex;
  margin: 0 auto;
  align-items: center;
}

.logo {
  width: 137px;
  height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
}
.logo .logoimage {
  width: 75px;
}
.exploreText {
  font-size: 10px;
  font-style: italic;
}
.plusText {
  font-size: 10px;
  margin: 0 2px;
  text-transform: capitalize;
  font-style: italic;
  color: #df3411;
}
.logo .goldenStar {
  width: 10px;
  height: 10px;
}

.searchInputContainer {
  width: 560px;
  position: relative;
  background: #fff;
  height: 36px;
  border-radius: 1px;
  overflow: hidden;
  display: flex;
}
.searchInput {
  width: 518px;
  border: 0;
  height: 36px;
  outline: none;
  box-sizing: border-box;
  padding: 0 10px;
}
.searchIconContainer {
  display: flex;
  flex: 1 1;
  justify-content: center;
  align-items: center;
}
.rightMenu {
  display: flex;
  flex: 1 1;
  justify-content: flex-end;
  align-items: center;
}
.loginButton {
  display: block;
  width: 100px;
  height: 30px;
  background: #fff;
  text-align: center;
  line-height: 30px;
  color: #2874f0;
  font-weight: 500;
  font-size: 16px;
  cursor: pointer;
}
.more,
.fullName {
  font-weight: bold;
  color: #fff;
  display: flex;
  align-items: center;
  margin: 0 20px;
  cursor: pointer;
}
.cart {
  font-weight: bold;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 20px;
}

.authContainer {
  color: #212121;
  margin: 0 auto;
  border-radius: 2px;
  height: 528px;
  max-width: 750px;
  min-width: 650px;
}
.row {
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  height: 528px;
  max-width: 750px;
  min-width: 650px;
}

.leftspace {
  width: 40%;
  height: 100%;
  color: #fff;
  letter-spacing: 1px;
  background-image: url(data:image/png;base64,);
  background-color: #2874f0;
  background-repeat: no-repeat;
  background-position: center 85%;
  padding: 40px 33px;
  box-sizing: border-box;
}

.rightspace {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  align-items: center;
}

.loginInputContainer {
  width: 100%;
  box-sizing: border-box;
  padding: 0 30px;
  margin: 40px 0;
}

/* Material Modal */
.modalFixedBg {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #000;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 12;
  display: flex;
  justify-content: center;
  align-items: center;
}
.modalContainer {
  min-width: 400px;
  max-width: 100%;
  min-height: 200px;
  max-height: 90vh;
  background: #fff;
  border-radius: 4px;
  margin: 0 auto;
  overflow-x: hidden;
}
.modalClose {
  position: absolute;
  color: #fff;
  right: -20px;
  font-size: 20px;
}

/* Material Input */
.materialInput {
  width: 100%;
  min-width: 300px;
  height: 30px;
  border-bottom: 2px solid #2874f0;
  position: relative;
  /* background-color: red; */
  margin-top: 25px;
}
.materialInput input {
  width: 100%;
  flex: 1 1;
  outline: none;
  border: none;
  z-index: 1;
  background: transparent;
  line-height: 30px;
}
.materialInput .label {
  position: absolute;
  line-height: 30px;
  color: grey;
  z-index: 0;
  font-size: 12px;
  letter-spacing: 1px;
  transition: all 0.1s ease-in-out;
}
.materialInput .label.focus {
  top: -20px !important;
}

/* Material Button */
.materialButton {
  width: 100%;
  display: inline-block;
  padding: 15px;
  font-size: 15px;
  outline: none;
  background-color: #fb641b;
  color: #ffffff;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2);
  border: none;
  cursor: pointer;
  border-radius: 2px;
}
.materialButton:hover {
  box-shadow: 0 4px 6px 0 rgba(0, 0, 0, 0.12);
}

/* Material dropdown */
.headerDropdownContainer {
  position: relative;
  display: inline-block;
}
.firstmenu {
  padding: 15px 20px;
  border-bottom: 1px solid #eee;
  font-size: 12px;
  font-weight: 500;
  display: flex;
  justify-content: space-between;
}
.dropdown {
  right: 50%;
  position: absolute;
  width: 240px;
  -webkit-transform: translateX(50%);
          transform: translateX(50%);
  z-index: 1;
  display: none;
}
.dropdownMenu {
  background: #fff;
  box-shadow: 0 4px 16px 0 rgba(0, 0, 0, 0.2);
  background: #fff;
  border-radius: 2px;
}
.headerDropdownContainer:hover .dropdown {
  display: block;
}
.upArrowContainer {
  position: relative;
  width: 100%;
  height: 10px;
}
.upArrow {
  width: 0;
  height: 0;
  position: absolute;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 10px solid #ffffff;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
.headerDropdownMenu {
  margin: 0;
  padding: 0;
  background: #fff;
}
.headerDropdownMenu li {
  list-style: none;
}
.headerDropdownMenu li a {
  display: block;
  padding: 15px 20px;
  box-sizing: border-box;
  font-size: 14px;
  border-bottom: 1px solid #eee;
  color: #212121;
  text-decoration: none;
}
.headerDropdownMenu li a:hover {
  background-color: #f0f0f0;
  cursor: pointer;
}

.anchorButton {
  background: transparent;
  border: none;
  letter-spacing: 1px;
}
.buttonAddwebsite {
  border-radius: 20%;
  background-color: red;
}

.btn-search {
  border: 0px solid #116411;
  padding: 6px 0 6px 0;
  height: 4vh;
  margin-top: 1vh;
  width: 80px;
  color: rgb(255, 255, 255);
  border-radius: 6%;
  background-color: #116411;
}
.btn-search:hover {
  color: #116411;
  background-color: white;
  cursor: pointer;
  border: 1px solid #116411;
  border-radius: 6%;
}
.menuHeader {
  width: 100%;
  align-self: center;
  border-bottom: 2px solid #116411;
  height: 6vh;
  background: #fff;
  border-bottom: 1px solid #cecece;
  box-shadow: 0 2px 2px -2px #333;
  display: flex;
  flex-direction: row;
}
.inputHeader {
  color: rgb(19, 18, 18);
  font-size: 14px;
}
.inputHeader:focus {
  border: 1px solid white;
  outline: none;
}
.menuHeader ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.menuHeader > ul {
  display: flex;
  margin: 0 50px;
  position: relative;
}
.menuHeader > ul > li > span {
  display: block;
  line-height: 40px;
  cursor: pointer;
  padding: 0 20px;
  font-size: 14px;
}
.menuHeader > ul > li > span:hover {
  color: #2874f0;
}
.menuHeader > ul > li > ul {
  position: absolute;
  background: #fff;
  left: 0;
  right: 0;
  display: none;
  border: 1px solid #cecece;
  z-index: 1;
}
.menuHeader > ul > li:hover ul {
  display: block;
}
.menuHeader > ul > li > ul > li {
  margin: 0 20px;
  min-width: 150px;
}
.menuHeader > ul > li > ul > li {
  float: left;
}
.menuHeader > ul > li > ul > li > a {
  font-weight: bold;
  display: block;
}
.menuHeader > ul > li > ul > li a {
  padding: 3px 0;
  display: block;
  font-size: 12px;
  text-decoration: none;
  color: #707070;
}
.iconBts {
  font-size: 18px;
  margin-top: 12px;
  margin-left: 1rem;
}
.categoryStyle {
  font-size: 18px;
  color: red;
}

.hero-banner {
  min-height: 480px;
  display: flex;
  align-items: center;
  width: 100%;
}
.hero-banner.full {
  min-height: 580px;
}
.hero-banner.center {
  justify-content: center;
  text-align: center;
}
.hero-banner .container {
  position: relative;
  z-index: 1;
}
.hero-banner h1 {
  color: #2e1b6a;
}
h1.small {
  font-weight: 500;
}
.featured-category {
  display: table;
  width: 100%;
  text-align: left;
  margin-top: 1.5rem;
}
.featured-category ul {
  margin: 0;
  padding: 0;
}
.featured-category ul li {
  margin-right: 12px;
  list-style: none;
  display: inline-block;
}
.featured-category ul li:first-child {
  margin-right: 20px;
}
.featured-category ul li a {
  background: rgba(255, 255, 255, 0.15);
  color: #ffffff;
  padding: 4px 12px;
  border-radius: 2px;
  font-size: 13px;
  letter-spacing: 0.5px;
  transition: all 0.4s;
}
.header-promo {
  margin-bottom: 10px;
  margin-left: 0px;
  padding: 5px 10px 5px 5px;
  border-radius: 20px;
  background-color: #f4f5f7;
  color: #95979b;
  max-width: 100%;
  display: inline-block;
  font-size: 14px;
  text-decoration: none;
}
.header-promo.light {
  margin-bottom: 10px;
  margin-left: 0px;
  padding: 5px 10px 5px 5px;
  border-radius: 20px;
  background-color: #dae8f3;
  color: #eaf6ff;
  max-width: 100%;
  display: inline-block;
  font-size: 14px;
  text-decoration: none;
}
.header-promo .label {
  display: inline-block;
  min-height: 0px;
  padding: 2px 6px;
  border-radius: 50px;
  background-color: #df3411;
  color: #fff;
  font-size: 12px;
  font-weight: 400;
}
.header-promo-text {
  display: inline-block;
  margin-left: 5px;
  color: #44495c;
}
.text-info {
  color: #0b85ec !important;
}
.text-inverse {
  color: #3e4555 !important;
}
form.search-big-form .form-group .form-control {
  padding-left: 38px;
  border-radius: 0;
}
.l-radius {
  border-radius: 4px 0px 0px 4px !important;
}
.r-radius {
  border-radius: 0px 4px 4px 0px !important;
}
.search-big-form .form-control,
.search-big-form .btn {
  height: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.search-big-form
  .select2-container--default
  .select2-selection--single
  .select2-selection__rendered {
  line-height: 64px;
}
.search-big-form .select2-container--default .select2-selection--single {
  height: 64px;
}
.search-big-form.shadows {
  position: relative;
  background: transparent;
  box-shadow: 0px 0px 0px 9px rgb(255 255 255 / 20%);
  border-radius: 1px;
}
.steps {
  display: flex;
  flex-direction: row;
  border: 0px solid black;
  width: 100%;
}
.working-process {
  text-align: center;
  position: relative;
  margin-bottom: 30px;
}

.productDescriptionContainer {
  display: flex;
  box-sizing: border-box;
  padding: 10px;
}
.verticalImageStack {
  width: 67px;
  display: flex;
  flex-direction: column;
}
.thumbnail {
  width: 63px;
  height: 63px;
  border: 1px solid #f0f0f0;
  overflow: hidden;
  text-align: center;
}
.thumbnail img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}
.thumbnail.active {
  border: 2px solid #2874f0;
}
.productDescContainer {
  width: 485px;
}
.productDescImgContainer {
  width: 485px;
  height: 440px;
  overflow: hidden;
  text-align: center;
  border: 1px solid #f0f0f0;
}
.productDescImgContainer > img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}
.breed ul {
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
}
.breed ul li {
  list-style: none;
  margin-right: 5px;
  display: flex;
  align-items: center;
  font-size: 12px;
}
.breed ul li a {
  text-decoration: none;
  color: #777;
  display: inline-block;
  font-size: 12px;
  margin: 0 10px;
}
.productDetails {
  margin: 30px 10px;
  background-color: #cfcdcd;
  border: 1px solid #cfcdcd;
  border-radius: 4%;
  padding: 6px 0px 0px 8px;
  display: flex;
  flex-direction: column;
  align-items: start;
  width: 30vw;
}
.productDetails .productTitle {
  font-size: 18px;
  color: #212121;
  font-weight: 400;
}
.ratingCount {
  display: inline-block;
  background: #388e3c;
  color: #fff;
  font-weight: 400;
  font-size: 12px;
  border-radius: 3px;
  padding: 2px 5px;
}
.visitors {
  display: flex;
  flex-direction: column;
  border: 1px solid rgb(253, 253, 253);
  border-radius: 8%;
  background-color: #fcfcfc;
  width: 20rem;
  margin-bottom: 1rem;
}
.ratingNumbersReviews {
  font-size: 12px;
  color: #777;
  font-weight: 600;
  display: inline-block;
  margin: 0 10px;
}
.extraOffer,
.discount {
  color: #388e3c;
  font-size: 12px;
  margin: 10px 0px;
  font-weight: 600;
}
.priceContainer {
  align-items: center;
}
.price {
  margin-top: 8px;
}
.contact {
  color: rgb(134, 134, 134);
  width: 100%;
  font-size: 12px;
}
.description {
  color: #212121;
  font-size: 22px;
  border-bottom: 1px solid green;
}
.description:hover {
  background-color: #cfcdcd;
  cursor: pointer;
}
.userContact {
  width: 30vw;
}

.modal-header {
  padding: 5px 15px;
  height: 40px;
  width: 40px;
  background-color: #333;
}
.headUpdate {
  text-align: center;
  color: #939096;
