}
.value {
  font-size: 14px;
  font-family: sans-serif;
}
.productImgContainer {
  width: 100px;
  height: 100px;
  overflow: hidden;
  text-align: center;
}
.productImgContainer img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.profile_container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;

  height: 94vh;
}
.profileRow {
  margin-top: 14px;
  border: 1px solid #2874f0;
  height: 92vh;
}
.colprofile {
  background-color: white;
  width: 35%;
}
.profilePricture {
  padding-left: 40px;
  padding-top: 8px;
}
.userInfo {
  display: flex;
  flex-direction: row;
  align-self: center;
  border: 1px solid grey;
  width: 58%;
  padding-left: 20px;
}
.addwebsite {
  display: flex;
  flex-direction: column;
  width: 20%;
}

.RowAddWebsite {
  display: flex;
  flex-direction: column;
}
.ConainerUnderProfile {
  display: flex;
  flex-direction: row;
  width: 100%;
  border: 1px solid red;
}

.rowUnderProfile {
  display: flex;
  flex-direction: row;
  width: 1100px;
}
.itemModal {
  display: flex;
  flex-direction: column;
}

