:root {
  --surface-color: #fff;
  --curve: 40;
}

* {
  box-sizing: border-box;
}

body {
  font-family: "Noto Sans JP", sans-serif;
  background-color: #fef8f8;
}

.cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 2rem;
  gap: 2rem;
  margin: 4rem 5vw;
  margin-top: 4rem;
  padding: 0;
  list-style-type: none;
  width: 80%;
}
#cardHover:hover {
  background-color: #2a0550;
}

.card {
  position: relative;
  display: block;
  height: 14vh;
  border-radius: calc(40 * 1px);
  border-radius: calc(var(--curve) * 1px);
  overflow: hidden;
  text-decoration: none;
  border-top: #b9b8b8;
}
.action {
  display: flex;
  border: 0px solid black;
  justify-content: end;
  margin-right: 30px;
}

.card__image {
  width: 100%;
  height: 80vh;
}

.card__overlay {
  position: absolute;
  display: flex;
  flex-direction: row;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;

  background-color: #fff;

  background-color: var(--surface-color);
  transition: 0.2s ease-in-out;
  justify-content: space-between;
}
.card__overlay .btn_ED {
  margin-top: 1rem;
  width: 14%;
  display: flex;
  align-items: center;
  margin-right: 1rem;
}
.btn_ED .iconSite:first-child {
  color: #1e2d42;
  font-size: 24px;
  width: 60%;
}
.btn_ED .iconSite:nth-child(2) {
  color: #646161;
  font-size: 24px;
  width: 60%;
}
.btn_ED .btn {
  width: 80%;
  border: 1px solid white;
  background-color: white;
}
.btn_ED .btn:hover {
  cursor: pointer;
}
.card__header {
  background-color: red;
  padding-top: 20px;
  position: relative;
  display: flex;
  align-items: center;
  grid-gap: 2em;
  gap: 2em;
  padding: 2em;
  border-radius: calc(40 * 1px) 0 0 0;
  border-radius: calc(var(--curve) * 1px) 0 0 0;
  background-color: #fff;
  background-color: var(--surface-color);

  transition: 0.2s ease-in-out;
}

.card__arc {
  width: 80px;
  height: 80px;
  position: absolute;
  bottom: 100%;
  right: 0;
  z-index: 1;
}

.card__arc path {
  fill: #fff;
  fill: var(--surface-color);
  d: path("M 40 80 c 22 0 40 -22 40 -40 v 40 Z");
}

.card:hover .card__header {
  -webkit-transform: translateY(0);
          transform: translateY(0);
}

.card__thumb {
  flex-shrink: 0;
  width: 50px;
  height: 50px;
  border-radius: 12%;
}

.card__title {
  font-size: 1em;
  margin: 0 0 0.3em;
  color: #6a515e;
}

.card__tagline {
  display: block;
  margin: 1em 0;
  font-family: "MockFlowFont";
  font-size: 0.8em;
  color: #d7bdca;
}

.card__status {
  font-size: 0.8em;
  color: #d7bdca;
}

.card__description {
  padding: 0 2em 2em;
  margin: 0;
  color: #4955c4;
  font-family: "MockFlowFont";
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  overflow: hidden;
}
.card__description div {
  color: black;
}

